import { NextRequest, NextResponse } from "next/server"
import { activityLogs, pickupRequests, wasteLogs, users } from "@/app/api/_data"
import type { PickupRequest } from "@/app/api/_types"

type CreateRequestBody = {
  wasteId: string
  requesterId: string
  preferredTime?: string
  notes?: string
}

// GET /api/farmer/requests
// Returns pickup requests created by the farmer user (sorted by newest first).
// Public endpoint - uses x-user-id header
export async function GET(req: NextRequest) {
  try {
    // Get user ID from header (public access)
    const userId = req.headers.get("x-user-id")
    if (!userId) {
      // Return empty array instead of error to prevent frontend crashes
      return NextResponse.json([])
    }
    
    const { users } = await import("@/app/api/_data")
    const user = users.find((u) => u.id === userId && u.role === "farmer")
    if (!user) {
      // Return empty array instead of error to prevent frontend crashes
      return NextResponse.json([])
    }

    // Import transports to get pickup schedule information
    const { transports } = await import("@/app/api/_data")
    
    const items = pickupRequests
      .filter((p) => p.requesterId === user.id && p.requesterType === "farmer")
      .sort((a, b) => b.requestedAt.localeCompare(a.requestedAt))
      .map((p) => {
        // Include waste details
        const waste = wasteLogs.find((w) => w.id === p.wasteId)
        const cafeUser = users.find((u) => u.cafeId === waste?.cafeId)
        
        // Find transport/pickup schedule if exists
        const transport = p.transportId ? transports.find((t) => t.id === p.transportId) : null
        
        // Get café contact information (admin or staff user)
        const cafeContact = users.find((u) => u.cafeId === waste?.cafeId && (u.role === "admin" || u.role === "staff"))
        
        return {
          ...p,
          itemName: waste?.itemName,
          quantity: waste?.quantity,
          cafeName: cafeUser?.organizationName || "Green Café",
          cafeId: waste?.cafeId,
          // Include café contact information
          cafeContact: cafeContact ? {
            name: cafeContact.name,
            email: cafeContact.email,
            organizationName: cafeContact.organizationName,
          } : null,
          // Include pickup schedule from transport or pickupWindow
          pickupSchedule: p.pickupWindow || (transport ? {
            date: transport.pickupDate,
            from: transport.pickupWindow.from,
            to: transport.pickupWindow.to,
          } : null),
          transportDetails: transport ? {
            transportType: transport.transportType,
            assignedTo: transport.assignedTo,
            provider: transport.provider,
            notes: transport.notes,
          } : null,
        }
      })

    return NextResponse.json(items)
  } catch (error) {
    console.error("[Farmer Requests] Error:", error)
    // Return empty array instead of error to prevent frontend crashes
    return NextResponse.json([])
  }
}

// POST /api/farmer/requests
// Creates a pickup request for an organic/coffee pending waste item.
// Allows public requests - validates requesterId exists
export async function POST(req: NextRequest) {
  const body = (await req.json()) as Partial<CreateRequestBody>
  if (!body.wasteId || !body.requesterId) {
    return NextResponse.json({ error: "Missing wasteId or requesterId" }, { status: 400 })
  }

  // Validate requester exists and is a farmer user
  const { users } = await import("@/app/api/_data")
  const requester = users.find((u) => u.id === body.requesterId)
  if (!requester) {
    return NextResponse.json({ error: "Invalid requesterId" }, { status: 400 })
  }
  if (requester.role !== "farmer") {
    return NextResponse.json({ error: "Requester must be a farmer user" }, { status: 403 })
  }

  const waste = wasteLogs.find((w) => w.id === body.wasteId)
  if (!waste) {
    return NextResponse.json({ error: "Waste item not found" }, { status: 404 })
  }

  // Validate waste is available for farmer
  if (
    !((waste.type === "organic" || waste.type === "coffee") && 
      waste.status === "pending" && 
      waste.assignedTo === "farmer")
  ) {
    return NextResponse.json({ error: "Waste item is not available for farm pickup" }, { status: 400 })
  }

  // Check if this waste already has a pickup request
  const existingRequest = pickupRequests.find((p) => p.wasteId === waste.id)
  if (existingRequest) {
    return NextResponse.json({ error: "This waste item already has a pickup request" }, { status: 400 })
  }

  const now = new Date().toISOString()
  const newRequest: PickupRequest = {
    id: `p-${Date.now()}`,
    wasteId: waste.id,
    requesterId: requester.id,
    requesterType: "farmer",
    requestedAt: now,
    status: "pending",
    preferredTime: body.preferredTime,
    notes: body.notes,
  }

  pickupRequests.push(newRequest)

  // Add activity log with pickupRequested action
  activityLogs.push({
    id: `a-${Date.now()}`,
    userId: requester.id,
    actionType: "pickupRequested",
    itemName: waste.itemName,
    requesterType: "farmer",
    createdAt: now,
  })

  return NextResponse.json(newRequest, { status: 201 })
}

